<template>
  <v-dialog :value="true" persistent max-width="290">
    <v-card>
      <v-card-title class="headline"> Edit Task </v-card-title>
      <v-card-text class="primary--text">
        Task Edit :
        <v-text-field 
          v-model="taskTitle"
          @keyup.enter="saveTask"
          >
          </v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn 
          color="green darken-1" 
          text
           @click="$emit('close')">
          취소
        </v-btn>
        <v-btn
          color="red lighten-1"
          text
          @click="saveTask"
          :disabled="taskTitleInvalid"
        >
          수정
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ["task"],
  data(){
    return{
      taskTitle:null
    }
  },
  computed:{
    taskTitleInvalid(){
      return !this.taskTitle || this.taskTitle === this.task.title
    }
  },
  methods:{
    saveTask(){
      if(!this.taskTitleInvalid){
        let payload ={
        id:this.task.id,
        title:this.taskTitle
      }
      this.$store.dispatch('updateTaskTitle',payload)
      this.$emit('close')
      }
    }
  },
  mounted(){
    this.taskTitle = this.task.title
  }
};
</script>

